import { currencyPairs, ICurrencyPair } from '../types';
import { getMockRates } from '../utils/getMockRates';

export interface IGetRates {
  (currencyPair:currencyPairs):ICurrencyPair
}

export const getRates = <IGetRates>(currencyPair:currencyPairs):ICurrencyPair => {
  return getMockRates(currencyPair);
} 


