import { connect } from 'react-redux';
import { Home, IHomeProps, IHomeDispatchProps } from './';
import * as actions from '../../../actions/';
import { currencyPairs, transactionTypes } from '../../../types/';
import { IStoreState } from '../../../types/';

export const mapStateToProps = (state:IStoreState):IHomeProps  => ({
  currencyPair: state.currencyPair,
  rates: state.rates,
  notionalAmount: state.notionalAmount
});

export const mapDispatchToProps = (dispatch:any):IHomeDispatchProps => ({
  onUpdateCurrency: (currencyPair:currencyPairs) => 
    dispatch(actions.updateCurrencyPair(currencyPair)),
  onUpdateTransaction: (transactionType:transactionTypes) =>
    dispatch(actions.updateTransaction(transactionType)),
  onUpdateNotionalAmount: (amount:number) =>
    dispatch(actions.updateNotionalAmount(amount)),
  onResetState: () =>
    dispatch(actions.resetState())
});

export default connect(mapStateToProps, mapDispatchToProps)(Home);


