import * as React from 'react';
import AppHeader from './components/atoms/AppHeader';
import AppIntro from './components/atoms/AppIntro';
import AppTitle from './components/atoms/AppTitle';
import AppWrapper from './components/atoms/AppWrapper';
import { GlobalStyle, lightTheme } from './theme/globalStyle';
import { ThemeProvider } from './theme/styled-components';
import Home from './components/pages/Home/container';


const App = () =>
  <ThemeProvider theme={lightTheme}>
    <React.Fragment>
      <GlobalStyle  />
      <AppWrapper>
        <GlobalStyle />
        <AppHeader>
          <AppTitle>Lux FX Trader</AppTitle>
        </AppHeader>
        <AppIntro>
          Update notional amount and click on Buy / Sell ticker to purchase / sell FX
        </AppIntro>
        <Home />
      </AppWrapper>
    </React.Fragment>
  </ThemeProvider>
;

export default App;



