import * as React from 'react';
import styled from '../../../theme/styled-components';
import { currencyPairs } from '../../../types';

export interface SelectProps {
  className?:string,
  currencyPair: currencyPairs,
  handleUpdateCurrency: (evt:any) => void
}

const Select = (props:SelectProps) => (
  <div className={props.className}>
    <label>Select currency pair:</label>
    <select className={props.className} onChange={props.handleUpdateCurrency} value={props.currencyPair}>
      {
        Object.keys(currencyPairs).map(key => 
          <option key={key} value={currencyPairs[key]}>{currencyPairs[key]}</option>
        )
      }
    </select>
  </div>
);


const FXSelector = styled(Select)`
  label {
    padding: 1rem;
  }
  select {
    border: 1px solid #ccc;
    borderRadius:0;
    height:2rem;
    width: 5rem;
    background: #fff;
  }
`;

export default FXSelector;




