import { currencySymbols, currencyPairs } from '../types';

export const getMockRates = (currencyPair:currencyPairs) => {
  const currentSymbols = currencySymbols[currencyPair];
  const baseSymbol = currentSymbols.baseSymbol;
  const quoteSymbol = currentSymbols.quoteSymbol;
  const randomRate1 = +(Math.random() * 10).toFixed(2);
  const randomRate2 = +(Math.random() * 10).toFixed(2);
  const randomValue1 = +(Math.random() * 100).toFixed(1);
  const randomValue2 = +(Math.random() * 100).toFixed(1);

  return {
    baseSymbol,
    quoteSymbol,
    buy: {
      rate: randomRate1,
      value: randomValue1
    }, 
    sell: {
      rate: randomRate2,
      value: randomValue2
    }
  };
}


