import * as React from 'react';
import { ICurrencyPair, transactionTypes } from '../../../types';
import CurrencyPairTitle from '../../atoms/CurrencyPairTitle';
import ResetButton from '../../atoms/ResetButton';
import NotionalAmount from '../../molecules/NotionalAmount';
import StyledTicker from '../../molecules/Ticker';
import TickerPairContainer from '../../atoms/TicketPairContainer';

export interface IFXTickerProps {
  rates: ICurrencyPair,
  notionalAmount: number,
  isValidNotionalAmount: boolean,
  handleTickerClick:(evt:any) => void,
  handleNotionalAmtChange:(evt:any) => void,
  handleNotionalAmtBlur:(evt:any) => void,
  onResetState:(evt:any) => void
}

export const FXTicker = (props:IFXTickerProps) => {
  // const buyValue = Math.floor(props.rates.buy.value);
  // const buyValueDecimal = Math.floor((props.rates.buy.value - buyValue) * 10);
  // const sellValue = Math.floor(props.rates.sell.value);
  // const sellValueDecimal = Math.floor((props.rates.sell.value - sellValue) * 10);

  return (
    <section>
      <NotionalAmount onBlur={props.handleNotionalAmtBlur} hasError={!props.isValidNotionalAmount} type="number" value={props.notionalAmount} onChange={props.handleNotionalAmtChange} />
      <ResetButton onClick={props.onResetState}>Reset</ResetButton>
      <TickerPairContainer>
        <CurrencyPairTitle>{props.rates.baseSymbol} / {props.rates.quoteSymbol}</CurrencyPairTitle>
        <StyledTicker type={transactionTypes.SELL} onClick={props.handleTickerClick} disabled={!props.isValidNotionalAmount}>
          <div>
            <span>Ask {props.rates.baseSymbol}</span>
            {/* <span>{props.rates.buy.rate}</span> */}
          </div>
          <div className="rate">{props.rates.sell.rate}</div>
          {/* <div>{buyValue} <span>{buyValueDecimal}</span></div> */}
        </StyledTicker>
        <StyledTicker type={transactionTypes.BUY} onClick={props.handleTickerClick} disabled={!props.isValidNotionalAmount}>
          <div>
            <span>Bid {props.rates.baseSymbol}</span>
            {/* <span>{props.rates.sell.rate}</span> */}
          </div>
          <div className="rate">{props.rates.buy.rate}</div>
          {/* <div>{sellValue} <span>{sellValueDecimal}</span></div> */}
        </StyledTicker>
      </TickerPairContainer>
    </section>
  );
};

export default FXTicker;




