import * as React from 'react';
import * as NotificationSystem from 'react-notification-system';
import { ICurrencyPair, currencyPairs, transactionTypes } from '../../../types';
import FXTicker from '../../organisms/FXTicker';
import FXSelector from '../../molecules/FXSelector';

export interface IHomeState {
  isValidNotionalAmount:boolean
}

export interface IHomeProps {
  currencyPair: currencyPairs,
  rates: ICurrencyPair,
  notionalAmount: number
}

export interface IHomeDispatchProps {
  onUpdateCurrency:(currencyPair:currencyPairs) => void,
  onUpdateTransaction:(type:transactionTypes) => void,
  onUpdateNotionalAmount:(amount:number) => void,
  onResetState:(evt:any) => void
}

export class Home extends React.Component<IHomeProps & IHomeDispatchProps, IHomeState> {
  notificationSystem:any;
  
  constructor(props:IHomeProps & IHomeDispatchProps) {
    super(props);
    this.state = {
      isValidNotionalAmount: true
    };
  }

  addNotification = (type:transactionTypes) => {
    let transactionAmount;
    let level;
    let message;

    switch(type) {
      case transactionTypes.BUY:
        transactionAmount = (this.props.notionalAmount * this.props.rates.buy.rate).toFixed(2);
        level = 'error';
        message = `You sold ${this.props.notionalAmount} ${this.props.rates.baseSymbol},
          in exchange for ${transactionAmount} ${this.props.rates.quoteSymbol}`;
        break;
      case transactionTypes.SELL:
        transactionAmount = (this.props.notionalAmount / this.props.rates.sell.rate).toFixed(2);
        level = 'success';
        message = `With ${this.props.notionalAmount} ${this.props.rates.quoteSymbol},
          you bought ${transactionAmount} ${this.props.rates.baseSymbol}`;
        break;
    }
    this.notificationSystem.addNotification({
      message,
      level
    });
    
  }

  componentDidMount = () => {
    this.notificationSystem = this.refs.notificationSystem;
  }

  handleUpdateCurrency = (evt:any) => {
    const selection = evt.target.value;
    this.props.onUpdateCurrency(selection); 
  }

  handleTickerClick = (type:transactionTypes) => {
    this.props.onUpdateTransaction(type);
    if (this.state.isValidNotionalAmount) {
      this.addNotification(type);
    }
  }

  handleNotionalAmtChange = (evt:any) => {
    const amount = evt.target.value;
    const isValidNotionalAmount = amount !== '';
    this.setState({
      isValidNotionalAmount
    });
    this.props.onUpdateNotionalAmount(isValidNotionalAmount ? +amount : amount);
  }

  handleNotionalAmtBlur = (evt:any) => {
    const amount = evt.target.value;
    this.setState({
      isValidNotionalAmount: (amount !== '')
    });
  }

  render() {
    return (
      <div>
        <NotificationSystem ref="notificationSystem" />
        <FXSelector currencyPair={this.props.currencyPair} handleUpdateCurrency={this.handleUpdateCurrency} />
        <FXTicker
          rates={this.props.rates}
          handleTickerClick={this.handleTickerClick}
          handleNotionalAmtChange={this.handleNotionalAmtChange}
          notionalAmount={this.props.notionalAmount}
          onResetState={this.props.onResetState}
          handleNotionalAmtBlur={this.handleNotionalAmtBlur}
          isValidNotionalAmount={this.state.isValidNotionalAmount}
        />
        
      </div>
    );
  }
}

export default Home;



