import * as constants from '../constants';
import { currencyPairs, ICurrencyPair, transactionTypes } from '../types';

export interface ICurrencySelectAction {
    type: constants.UPDATE_CURRENCY_PAIR,
    payload: currencyPairs
}

export function updateCurrencyPair(currencyPair:currencyPairs): ICurrencySelectAction {
    return {
        type: constants.UPDATE_CURRENCY_PAIR,
        payload: currencyPair
    };
}

export interface ITransactionSelectAction {
    type: constants.UPDATE_TRANSACTION,
    payload: transactionTypes
}

export function updateTransaction(transactionType:transactionTypes): ITransactionSelectAction {
    return {
        type: constants.UPDATE_TRANSACTION,
        payload: transactionType
    };
}

export interface IUpdateRates {
    type: constants.UPDATE_RATES,
    payload: ICurrencyPair
}

export function updateRates(rates:ICurrencyPair):IUpdateRates {
    return {
        type: constants.UPDATE_RATES,
        payload: rates
    };
}

export interface IUpdateNotionalAmount {
    type: constants.UPDATE_NOTIONAL_AMOUNT,
    payload: number
}

export function updateNotionalAmount(amount:number):IUpdateNotionalAmount {
    return {
        type: constants.UPDATE_NOTIONAL_AMOUNT,
        payload: amount
    };
}

export interface IResetState {
    type: constants.CLEAR_STATE
}

export function resetState():IResetState {
    return { 
        type: constants.CLEAR_STATE
    };
}


