import { Middleware, MiddlewareAPI, Dispatch, AnyAction } from 'redux';
import * as actions from '../actions';
import * as constants from '../constants';
import { IGetRates } from '../services/getRates';

export const createAppMiddleware = (getRates:IGetRates):Middleware => {
  return (api: MiddlewareAPI<any>) => (next: Dispatch<AnyAction>) => <A extends AnyAction>(action: A) => {
    if (action.type === constants.UPDATE_CURRENCY_PAIR) {
      const rates = getRates(action.payload);
      api.dispatch(actions.updateRates(rates));
    } 
   return next(action);
  };
}



