import * as React from 'react';
import styled from '../../../theme/styled-components';

const Ticker = (props:any) => {
  const handleClick = (evt:any) => {
    evt.preventDefault();
    props.onClick(props.type);
  }
  return (
    <a className={props.className} data-type={props.type} onClick={handleClick}>
      {props.children}
    </a>
  );
};

const StyledTicker = styled(Ticker)`
  background-color: ${props => props.type ? props.theme[props.type].background : props.theme.foreground};
  cursor: ${props => props.disabled ? 'not-allowed': 'pointer'};
  padding: 1rem 3rem;
  margin:0.5rem;
  color: ${props => props.theme.background};
  width:4rem;
  display:inline-block;
  vertical-align: top;
  user-select: none; 
  .rate {
    font-size:1.4rem;
    font-weight: bold;
  }
`;

export default StyledTicker;


