export enum currencyPairs {
  EURUSD = 'EURUSD',
  GBPUSD = 'GBPUSD',
  CHFUSD = 'CHFUSD'
}

export enum transactionTypes {
  BUY = 'BUY',
  SELL = 'SELL'
}

export interface ITradeDetail {
  rate: number,
  value: number
}

export interface ICurrencyPair {
  baseSymbol: string,
  quoteSymbol: string,
  buy: ITradeDetail,
  sell: ITradeDetail
}

export interface IStoreState {
  currencyPair: currencyPairs,
  rates: ICurrencyPair,
  transactionType: transactionTypes,
  notionalAmount: number
}

export const currencySymbols = {
  EURUSD: {
    baseSymbol: 'EUR',
    quoteSymbol: 'USD'
  },
  GBPUSD: {
    baseSymbol: 'GBP',
    quoteSymbol: 'USD'
  },
  CHFUSD: {
    baseSymbol: 'CHF',
    quoteSymbol: 'USD'
  }
}



