import { combineReducers } from 'redux';
import { ICurrencySelectAction, IUpdateRates, ITransactionSelectAction, IUpdateNotionalAmount } from '../actions';
import { IStoreState, currencyPairs, transactionTypes } from '../types';
import * as constants from '../constants';
import { ICurrencyPair } from '../types';
import { getMockRates } from '../utils/getMockRates';

const initialCurrencyPair = currencyPairs.GBPUSD;
const initialCurrencyPairRate = getMockRates(initialCurrencyPair);
const initialTransaction = transactionTypes.BUY;
const initialNotionalAmount = 2000;

export const appReducer = combineReducers<IStoreState>({
  currencyPair: (state:currencyPairs = initialCurrencyPair, action:ICurrencySelectAction) => 
      action.type === constants.UPDATE_CURRENCY_PAIR ? action.payload : state,
  rates: (state:ICurrencyPair = initialCurrencyPairRate, action:IUpdateRates) =>
    action.type === constants.UPDATE_RATES ? action.payload : state,
  transactionType: (state:transactionTypes = initialTransaction, action:ITransactionSelectAction) =>
    action.type === constants.UPDATE_TRANSACTION ? action.payload : state,
  notionalAmount: (state:number = initialNotionalAmount, action:IUpdateNotionalAmount) => 
    action.type === constants.UPDATE_NOTIONAL_AMOUNT ? action.payload : state
  
});

export const rootReducer = (state: IStoreState, action: any): IStoreState => {
  if (action.type === constants.CLEAR_STATE) {
    return {
      ...appReducer(undefined, action)
    };
  }
  return appReducer(state, action)
};


