import * as React from 'react';
import styled from '../../../theme/styled-components';

export interface NotionalProps {
  className?:string,
  hasError: boolean,
  type: string,
  value: number,
  onChange: (evt:any) => void,
  onBlur: (evt:any) => void
}

const StyledErrorMessage = styled.p`
  padding: 0 0 0 1rem;
  margin-bottom:0;
  color: red
`;

const Amount = (props:NotionalProps) => (
  <div className={props.className}>
    {props.hasError && <StyledErrorMessage>Please enter a valid amount</StyledErrorMessage>}
    <label>Notional Amount:</label>
    <input onChange={props.onChange} onBlur={props.onBlur} type={props.type} value={props.value} />
  </div>
);


const NotionalAmount = styled(Amount)`
  label {
    padding: 1rem;
  }
  input {
    margin: 1rem;
    padding: 0.5rem;
  }
`;

export default NotionalAmount;


